<template>
	<div>
		<v-container fluid class="pa-6">
			<v-row>
				<v-col align="center">
          <h2>We'll do this in categories, so you aren't too overwhelmed</h2>
        </v-col>
			</v-row>
			<v-row>
				<v-card class="mx-auto test-card" dark>
					<v-card-title class="text-h6 font-weight-regular justify-space-between">
						<span>{{title}}</span>
					</v-card-title>

					<v-window v-model="step" dark>
						<v-window-item v-for="(category, index) in fetList.categories" :key="category.id" :value="category.id">
							<question-component v-for="(fetish, index2) in category.values" :key="fetish.name" :fetish="fetish" @changeRating="(event) => changeRating(event, index, index2)"/>
						</v-window-item>
					</v-window>

					<v-card-actions>
						<v-btn
							:disabled="step === 1"
							text
							@click="previousStep"
						>
							Back
						</v-btn>
						<v-spacer></v-spacer>
						<v-btn
							v-if="step !== maxStep"
							color="primary"
							depressed
							@click="nextStep"
						>
							Next
						</v-btn>
						<div v-else>
							<v-btn
								color="primary"
								depressed
								@click="downloadJSON"
							>
								Finish JSON
							</v-btn>
							<v-btn
								color="primary"
								depressed
								@click="downloadTXT"
							>
								Finish TXT
							</v-btn>
							<!-- <v-btn
								color="primary"
								depressed
								@click="downloadPDF"
							>
								Finish PDF
							</v-btn> -->
						</div>
					</v-card-actions>
				</v-card>
			</v-row>
		</v-container>
	</div>
</template>

<script>
import QuestionComponent from '@/components/QuestionComponent.vue';
import fetishList from "../assets/fetishList.json";
import FileSaver from "file-saver";
import { PDFDocument, StandardFonts, rgb } from 'pdf-lib';

export default {
  name: 'TestView',
	components: {
    QuestionComponent
	},
	data: () => ({
		step: 1,
		fetList: fetishList,
	}),
	computed: {
		title() {
			return this.fetList.categories[this.step].name;
		},
		maxStep() {
			return this.fetList.categories.length -1;
		}
	},
	methods: {
		downloadJSON() {
			var blob = new Blob([JSON.stringify(this.fetList)], {type: "text/JSON;charset=utf-8"});
			FileSaver.saveAs(blob, "Fetest.json");
		},
		async downloadPDF() {
			const pdfDoc = await PDFDocument.create()
			const Helvetica = await pdfDoc.embedFont(StandardFonts.Helvetica)

			const page = pdfDoc.addPage()
			this.fetList.categories.forEach(category => {
				category.values.forEach(fetish => {
					page.drawText(`${fetish.name}`, {
						size: 16,
						font: Helvetica,
						color: rgb(0, 0, 0),
					})
					switch(fetish.type) {
					case "a":
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.lookAt)} looking at`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.lookedAt)} others looking at my`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.interactWith)} interacting with`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.interactedWith)} others iteracting with my`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.fantasyMe)} fantasizing about`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.fantasyOthers)} fantasizing about having`);
						break;
					case "f":
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.toMe)} ((about) being a receiver of)`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.toSomeone)} ((about) giving/causing)`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.fantasyMe)} (to) fantasizing about (being a receiver of)`);
						this.writeLine(page, Helvetica, fetish.name, `I('m) ${this.parseNumber(fetish.fantasyOthers)} fantasizing about (giving/causing)`);
						break;
				}
				})
			})
			const pdfBytes = await pdfDoc.save()
			var blob = new Blob([pdfBytes]);
			FileSaver.saveAs(blob, "Fetest.pdf", {type: "text/plain;charset=utf-8"});
		},
		downloadTXT() {
			let outputData = [];
			this.fetList.categories.forEach(category => {
				category.values.forEach(fetish => {
					switch(fetish.type) {
					case "a":
						outputData.push(`I('m) ${this.parseNumber(fetish.lookAt)} looking at ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.lookedAt)} others looking at my ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.interactWith)} interacting with ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.interactedWith)} others iteracting with my ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.fantasyMe)} fantasizing about ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.fantasyOthers)} fantasizing about having ${fetish.name}\n\n`);
						break;
					case "f":
						outputData.push(`I('m) ${this.parseNumber(fetish.toMe)} ((about) being a receiver of) ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.toSomeone)} ((about) giving/causing) ${fetish.name}\n`)
						outputData.push(`I('m) ${this.parseNumber(fetish.fantasyMe)} (to) fantasizing about (being a receiver of) ${fetish.name}\n`);
						outputData.push(`I('m) ${this.parseNumber(fetish.fantasyOthers)} fantasizing about (giving/causing) ${fetish.name}\n\n`);
						break;
					}
				})
			})
			
			var blob = new Blob([outputData.join("")], {type: "text/JSON;charset=utf-8"});
			FileSaver.saveAs(blob, "Fetest.txt");
		},
		writeLine(page, font, fetishName, result) {
			page.drawText(`${result} ${fetishName}`, {
				size: 12,
				font: font,
				color: rgb(0, 0, 0),
			})
		},
		parseNumber(number){
			switch(number) {
				case 1:
					return "hate"
				case 2:
					return "dislike"
				case 3:
					return "indifferent"
				case 4:
					return "like"
				case 5:
					return "love"
				default:
					return "error"
			}
		},
		changeRating(value, category, fetish) {
			this.fetList.categories[category].values[fetish][value.type] = value.value;
		},
		nextStep() {
			this.step++;
			window.scrollTo(0, 0);
		},
		previousStep() {
			this.step++;
			window.scrollTo(0, 9999);
		}
	},
	mounted() {
		this.fetList.categories.forEach(category => {
			category.values.forEach(fetish => {
				switch(fetish.type) {
					case "a":
						fetish.lookAt = 1;
						fetish.lookedAt = 1;
						fetish.interactWith = 1;
						fetish.interactedWith = 1;
						fetish.fantasyMe = 1;
						fetish.fantasyOthers = 1;
						break;
					case "f":
						fetish.toMe = 1;
						fetish.toSomeone = 1;
						fetish.fantasyMe = 1;
						fetish.fantasyOthers = 1;
						break;
				}
			})
		})
	}
}
</script>

<style>
.test-card {
	width: 100%;
}
</style>