<template>
  <v-card>
    <v-card-text>
        <h3>{{fetName}}</h3>
        <h6>{{fetSubtext}}</h6>
        <div v-if="fetish.type === `f`"> 
            <label>I('m) ____ (being a receiver of) {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `toMe`)"></v-slider>
            <label>I('m) ____ (giving/causing) {{fetName}} </label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `toSomeone`)"></v-slider>
            <label>I('m) ____ fantasizing about (being a receiver of) {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `fantasyMe`)"></v-slider>
            <label>I('m) ____ fantasizing about (giving/causing) {{fetName}} </label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `fantasySomeone`)"></v-slider>
        </div>
        <div v-if="fetish.type === `a`"> 
            <label>I('m) ____ looking at {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `lookAt`)"></v-slider>
            <label>I('m) ____ others looking at my {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `lookedAt`)"></v-slider>
            <label>I('m) ____ interacting with {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) => changeRating(event, `interactWith`)"></v-slider>
            <label>I('m) ____ others iteracting with my {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) =>  changeRating(event, `interactedWith`)"></v-slider>
            <label>I('m) ____ fantasizing about {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) =>  changeRating(event, `fantasyOthers`)"></v-slider>
            <label>I('m) ____ fantasizing about having {{fetName}}</label><v-slider ticks="always" :tick-labels="ticksLabels" tick-size="4" step="1" class="sliderStyle" min="1" max="5" @change="(event) =>  changeRating(event, `fantasyMe`)"></v-slider>
        </div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
    data: () => ({
        ticksLabels: [
            'Hate',
            'Dislike',
            'Indifferent',
            'Like',
            'Love'
        ],
    }),
    props: {
        fetish: {
            type: Object,
            required: true
        },
    },
    computed: {
        fetName() {
            return this.fetish.name;
        },
        fetSubtext() {
            return this.fetish.subtext;
        }
    },
    methods: {
        changeRating(value, type) {
            this.$emit("changeRating", {value, type});
        } 
    }
}
</script>

